import axios from 'axios'
import store from '@/store/store'

export default () => {
    var url;
    if (process.env.NODE_ENV === 'development') {
        url = `http://localhost:3000/`
    } else {
        url = `https://songtaps.herokuapp.com/`
    }
    console.log(url)
    return axios.create({
        baseURL: url,
        headers: {
            Authorization: `Bearer ${store.state.token}`
        }
    })
}




